<template  >
    <div >
                <el-row style="margin:0 1rem;"><el-col :span="4" :offset="20" style="text-align:right;">
                        <el-button type="primary"  size="mini" @click="open1" style="margin-bottom:1rem; padding:0.8rem 2.6rem;">添加</el-button>
                <!-- <el-button  @click="exportExcel"  size="medium" type="success">导出</el-button> -->
                    </el-col>  
                </el-row>
            <el-table
                :data="tableData"
                border
                style="width: 100%">
                <el-table-column
                prop="id"
                label="ID"
                >
                </el-table-column>
                <el-table-column
                prop="username"
                label="用户名"
                >
                </el-table-column>
                <el-table-column
                prop="password"
                label="密码"
                >
                </el-table-column>
                <el-table-column
                prop="level"
                label="账号类型"
                >
                </el-table-column>
                <el-table-column
                prop="affiliation"
                label="账号归属地"
                >
                </el-table-column>
                <el-table-column
                prop="telphone"
                label="联系方式"
                >
                </el-table-column>
                <el-table-column
                label="操作"
                >
                    <template slot-scope="scope">
                        <el-button  @click="handleClick(scope.row)" type="text" size="small" >修改</el-button> | <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">移除</el-button>
                    </template>
                </el-table-column>
            </el-table>
             <div class="block" style="text-align:center;margin-top:1rem;">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentpage"
                :page-size="5"
                layout="prev, pager, next"
                :total="talee">
                </el-pagination>
            </div>
           <!-- 修改 -->
            <div v-if="show1" style="width:100vw;height:100vh;position: fixed;top:0;left:0;z-index: 2;">
                <modification @closeparent='closeparent' @closeparent2='closeparent2'></modification>
            </div>
            <!-- 添加 -->
            <div v-if="show2" style="width:100vw;height:100vh;position: fixed;top:0;left:0;z-index: 2;">
                <register @closeparent1='closeparent1' @closeparent3="closeparent3"></register>
            </div>
    </div>
</template>
<script>
// 引入
import ajax from '../common/ajax';
import modification from './modification';
import register from './register';

export default {
    name:'management',
    inject:['reload'],
    props:{
    // 总数据
        tableD:'',
    },
    inject:['reload'],
    data(){
        return{
                show1:false,
                show2:false,
                pageSize:5,
                currentpage:1,
                    tableDatas:[],
                // 每一页数据
                tableData:[],
                talee:1,
                perval:5,
                userSelects:{
                    uid:window.localStorage.getItem('uid'),
                },
        }
    },
        mounted(){
             
  },
  methods: {
      extrat(){
            var url = "http://jenk.gd.cn:8080/FB/user/select";
                var _this = this;
            var obj = _this.userSelects;
            ajax.inAjaxpost(url,obj,(data)=>{
                for(var i = 0 ; i < data.user.length ; i++){
                    if( data.user[i].level == '1') {
                        data.user[i].level = '操作管理员';
                    }
                    else  if( data.user[i].level == '2') {
                        data.user[i].level = '站点管理员';
                     
                    }else  if( data.user[i].level == '3') {
                        data.user[i].level = '公司管理员';
                   
                    } else  if( data.user[i].level == '4') {
                        data.user[i].level = '超级管理员';
                    }
                    else{
                        return;
                    }
                }
                 for(var i = 0 ; i < data.user.length ; i++){
                    window.sessionStorage.getItem('username');
                    if(data.user[i].username  ==   window.localStorage.getItem('saveusername')){
                            data.user.splice(i,1)    
                    }
                }
                    _this.tableData = data.user.slice(0,_this.perval);
                    _this.tableDatas = data.user;
                    _this.talee = _this.tableDatas.length;
            });
              
      },
      handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.tableData=this.tableDatas.slice((val-1)*this.pageSize,val*this.pageSize)
        },
       closeparent3(){
           this.$confirm('是否添加管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '取消添加',
                cancelButtonText: '继续添加'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '取消添加',
                });
                this.show2 = false;
                   this.extrat();
                this.reload;
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续添加'
                    : '停留在当前页面'
                })
            });
         },
         closeparent2(){
             this.$confirm('是否修改管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '取消修改',
                cancelButtonText: '继续修改'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '取消修改',
                });
                this.show1 = false;
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续修改'
                    : '停留在当前页面'
                })
            });
         },
        closeparent(){
            this.show1 = false;
                 this.reload;
            this.extrat();
              this.currentpage = 1;
               this.tableData=this.tableDatas.slice(0,this.perval)
         },
        closeparent1(){
            this.show2 = false;
               this.reload;
           this.extrat();
                   this.currentpage = 1;
                   this.tableData=this.tableDatas.slice(0,this.perval)
        },
         open1() {
          this.show2 = true;
        },
      handleClick(row) {
        //   当前数据row
        this.show1 = true;
        window.sessionStorage.setItem("uid",row.id);
        window.sessionStorage.setItem("affiliation",row.affiliation);
        window.sessionStorage.setItem("id",row.id);
        window.sessionStorage.setItem("level",row.level);
        window.sessionStorage.setItem("login",row.login);
        window.sessionStorage.setItem("password",row.password);
        window.sessionStorage.setItem("remark",row.remark);
        window.sessionStorage.setItem("telphone",row.telphone);
        window.sessionStorage.setItem("username",row.username);

      },



    deleteRow(index, rows) {
        //    删除当前的数据
    this.$confirm('是否确认删除？', '确认信息', {
          distinguishCancelAndClose: true,
          confirmButtonText: '确认删除',
          cancelButtonText: '取消删除'
        })
          .then(() => {
            this.$message({
              type: 'info',
              message: '已经删除'
            });
        // 提交删除的 数据
             var url = "http://jenk.gd.cn:8080/FB/user/delete";
               var _this = this;
                var obj1 =  rows.splice(index, 1)[0];
                var  id =  obj1.id;
                var  username =  obj1.username;
                var  password = obj1.password;
                var  telphone = obj1.telphone;
                var  remark =  obj1.remark;
                var  level = obj1.level;
                var  affiliation = obj1.affiliation;
                var  login =  obj1.login;
                var uid = _this.userSelects.uid ;
                var  obj =   {id, username,password, telphone ,remark , level ,affiliation , login,uid } 
                ajax.inAjaxpost(url,obj,(data)=>{
                });
                  this.reload;
                this.extrat();
                      this.currentpage = 1;
                       this.tableData=this.tableDatas.slice(0,_this.perval)
          })
          .catch(action => {
            this.$message({
              type: 'info',
              message: action === 'cancel'
                ? '取消删除'
                : '停留在当前页面'
            })
          });
      }
    },
     components:{
      register,
      modification,
    },
    watch: {
        tableD: function (vals){
            var _this = this;
            _this.tableData = vals.slice(0,_this.perval);
            _this.tableDatas = vals;
            _this.talee = vals.length;
        }
    },
}
</script>
<style scoped>
    .el-button+.el-button {
    margin-left: 0px;
}
</style>




