import Vue from 'vue'
import Router from 'vue-router'
import backstage from '@/components/backstage'
import login from '@/components/login'
import register from '@/components/register'
import modification from '@/components/modification'
import modification2 from '@/components/modification2'
import modification1 from '@/components/modification1'
import management from '@/components/management'
import management1 from '@/components/management1'
import revealUser from '@/components/revealUser'
import maintain from '@/components/maintain'
import station from '@/components/station'
import register1 from '@/components/register1'
import register2 from '@/components/register2'
import modificationstation from '@/components/modificationstation'
import registerstation from '@/components/registerstation'

import modification3 from '@/components/modification3'
import sampling from '@/components/sampling'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    }, 
    {
      path: '/maintain',
      name: 'maintain',
      component: maintain
    }, 
     {
      path: '/modificationstation',
      name: 'modificationstation',
      component: modificationstation
    },
    {
      path: '/registerstation',
      name: 'registerstation',
      component: registerstation
    },
    {
      path: '/revealUser',
      name: 'revealUser',
      component: revealUser
    }, 
    {
      path: '/station',
      name: 'station',
      component: station
    }, 
    {
      path: '/backstage',
      name: 'backstage',
      component: backstage
    }, 
    {
      path: '/sampling',
      name: 'sampling',
      component: sampling
    }, 
    {
      path: '/management',
      name: 'management',
      component: management
    }, 
    {
      path: '/management1',
      name: 'management1',
      component: management1
    }, 
  
    {
      path: '/modification',
      name: 'modification',
      component: modification
    },
    {
      path: '/modification2',
      name: 'modification2',
      component: modification2
    },
    {
      path: '/modification3',
      name: 'modification3',
      component: modification3
    },
    {
      path: '/modification1',
      name: 'modification1',
      component: modification1
    },    
    {
      path: '/register',
      name: 'register',
      component: register
    },
    {
      path: '/register1',
      name: 'register1',
      component: register1
    },
    {
      path: '/register2',
      name: 'register2',
      component: register2
    },
 
  ]
})



