<template  >
    <div >
                <el-row style="margin:0 1rem;"><el-col :span="4" :offset="20" style="text-align:right;">
                        <el-button type="primary"  size="mini" @click="open1" style="margin-bottom:1rem; padding:0.8rem 2.6rem;">添加</el-button>
                    </el-col>  
                </el-row>
            <el-table
                :data="tableData"
                border
                style="width: 100%">
                <el-table-column
                prop="site_name"
                label="站点名"
                >
                </el-table-column>
                <el-table-column
                prop="sampling_num"
                label="取样数量"
                >
                </el-table-column>
                <el-table-column
                prop="equipment_num"
                label="设备数量"
                >
                </el-table-column>
      
                <el-table-column
                label="操作"
                >
                    <template slot-scope="scope">
                        <el-button  @click="handleClick(scope.row)" type="text" size="small" >修改</el-button> | <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">移除</el-button>
                    </template>
                </el-table-column>
            </el-table>
             <div class="block" style="text-align:center;margin-top:1rem;">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentpage"
                :page-size="5"
                layout="prev, pager, next"
                :total="talee">
                </el-pagination>
            </div>
           <!-- 修改 -->
        
            <div v-if="show1" style="width:100vw;height:100vh;position: fixed;top:0;left:0;z-index: 2;">
                <modificationstation @closeparent100000='closeparent100000' @closeparent200000='closeparent200000'></modificationstation>
            </div>
            <!-- 添加 -->
            <div v-if="show2" style="width:100vw;height:100vh;position: fixed;top:0;left:0;z-index: 2;">
                <registerstation @closeparent100000000='closeparent100000000' @closeparent300000="closeparent300000"></registerstation>
            </div>
    </div>
</template>
<script>
// 引入
import ajax from '../common/ajax';
import modificationstation from './modificationstation';
import registerstation from './registerstation';

export default {
    name:'station',
    inject:['reload'],
    props:{
    // 总数据
        tableD4:'',
    },
    data(){
        return{
                show1:false,
                show2:false,
                pageSize:5,
                currentpage:1,
                tableDatas:[],
                // 每一页数据
                tableData:[],
                talee:1,
                perval:5,
                
                userSelects:{
                
                    uid:window.localStorage.getItem('uid'),
                },
        }
    },
        mounted(){
             
  },
  methods: {
      extrat(){
            var url = "http://jenk.gd.cn:8080/FB/site/select";
                var _this = this;
            var obj = _this.userSelects;
            ajax.inAjaxpost(url,obj,(data)=>{
                _this.tableData = data.site.slice(0,_this.perval);
                _this.currentpage = 1;
                _this.tableDatas = data.site;
                _this.talee = data.site.length;
                })
      },
      handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.tableData=this.tableDatas.slice((val-1)*this.pageSize,val*this.pageSize)
        },
       closeparent100000000(){
           this.$confirm('是否添加管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '取消添加',
                cancelButtonText: '继续添加'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '取消添加',
                });
                    this.show2 = false;
                    this.extrat();
                    this.reload;
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续添加'
                    : '停留在当前页面'
                })
            });
         },
         closeparent200000(){
             this.$confirm('是否修改管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '取消修改',
                cancelButtonText: '继续修改'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '取消修改',
                });
                    this.show1 = false;
                    this.extrat();
                    this.reload;
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续修改'
                    : '停留在当前页面'
                })
            });
         },
        closeparent100000(){
            this.show1 = false;
            this.extrat();
            this.reload;
            this.currentpage = 1;
            this.tableData=this.tableDatas.slice(0,this.perval)
         },
        closeparent300000(){
            this.show2 = false;
            this.extrat();
            this.reload;
            this.currentpage = 1;
            this.tableData=this.tableDatas.slice(0,this.perval)
        },
         open1() {
          this.show2 = true;
        },
      handleClick(row) {
        this.show1 = true;
        window.sessionStorage.setItem("s_id",row.id);
        window.sessionStorage.setItem("site_name",row.site_name);
        window.sessionStorage.setItem("sampling_num",row.sampling_num);
        window.sessionStorage.setItem("equipment_num",row.equipment_num);
      },

    deleteRow(index, rows) {
        console.log(rows);
        this.$confirm('是否确认删除？', '确认信息', {
            distinguishCancelAndClose: true,
            confirmButtonText: '确认删除',
            cancelButtonText: '取消删除'
            })
            .then(() => {
                this.$message({
                    type: 'info',
                    message: '已经删除'
                });
                var url = "http://jenk.gd.cn:8080/FB/site/delete";
                    var _this = this;
                    var obj = {};
                    var id ="";
                    obj.uid = _this.userSelects.uid;
                    obj.id =rows[index].id;
                    ajax.inAjaxpost(url,obj,(data)=>{
                    });
                    this.reload;
                    this.extrat();
                    this.currentpage = 1;
                    this.tableData=this.tableDatas.slice(0,_this.perval)
                })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '取消删除'
                    : '停留在当前页面'
                })
            });
        }
    },
     components:{
      registerstation,
      modificationstation,
    },
    watch: {
        tableD4: function (vals){
            var _this = this;
            _this.tableData = vals.slice(0,_this.perval);
            _this.currentpage = 1;
            _this.tableDatas = vals;
            _this.talee = vals.length;
        }
    },
}
</script>
<style scoped>
    .el-button+.el-button {
    margin-left: 0px;
}
</style>




