<template>
    <div>
           <div style="position: absolute;top:0;left:0;bottom:0px;right:0px; background-color:#333;opacity: 0.5;z-index: 1;"></div>
        <div style="position: absolute;width: 100vw;z-index: 2;">
        <el-container>
            <el-main style="margin-top:2rem;">
                <el-row >
                    <el-col style="background-color:#fff;" :span="12" :offset="6">
                        <div >
                            <div class="changstyle1" >
                                  <div style="width:18px;height:18px;position:absolute;top:0rem;right:0.5rem;z-index:111">
                                          <el-button style="border:0px solid #fff;padding:0rem;" @click="cancel3">
                                                <img :src="cancel" alt="取消" width='25px'>
                                          </el-button>
                                </div>
                                <el-row>
                                    <el-col :span="24"><div class="changstyle2">添加</div></el-col>
                                </el-row>
                               <el-form :model="ruleForm2" status-icon  ref="ruleForm2" label-width="6rem" >
                                    <el-form-item label="站点名" prop="e_site_name">
                                        <el-input placeholder="请输入站点名" v-model="ruleForm2.e_site_name"></el-input>
                                    </el-form-item>
                                    <el-form-item style="line-height:-1rem;" label="检查设备" prop="devicename">
                                        <el-input placeholder="请输入检查设备" v-model="ruleForm2.devicename"></el-input>
                                    </el-form-item>
                                    <el-form-item style="line-height: 1rem!important;" label="设备属性" prop="attribute">
                                        <el-input placeholder="请输入设备属性" v-model="ruleForm2.attribute" ></el-input>
                                    </el-form-item>
                                    <el-form-item label="设备状态" prop="normal">
                                        <el-input placeholder="请输入设备状态" v-model="ruleForm2.normal" >
                                        </el-input>
                                    </el-form-item>
                                     <el-form-item label="温度" prop="temperature">
                                        <el-input placeholder="请输入温度" v-model="ruleForm2.temperature" >
                                        </el-input>
                                    </el-form-item>
                                    <el-form-item label="异常时间" prop="abnormal_time">
                                       <el-date-picker  v-model="ruleForm2.abnormal_time" type="datetime" placeholder="选择异常日期时间"></el-date-picker>
                                    </el-form-item>
                                   <el-form-item label="异常原因" prop="abnormal_cause">
                                        <el-input placeholder="请输入异常原因" v-model="ruleForm2.abnormal_cause">
                                        </el-input>
                                    </el-form-item>
                                    <el-form-item label="修复时间" prop="repair_time">
                                          <el-date-picker  v-model="ruleForm2.repair_time" type="datetime"  placeholder="选择修复日期时间"> </el-date-picker>
                                    </el-form-item>
                                     <el-form-item label="备注" prop="remark" >
                                        <el-input placeholder="请输入备注" v-model="ruleForm2.remark">
                                        </el-input>
                                    </el-form-item>
                                      <el-form-item label="图片" prop="image" >
                                        <el-input placeholder="请输入图片" v-model="ruleForm2.image"></el-input>
                                    </el-form-item>
                                        <el-button type="primary" @click="submitForm('ruleForm2')">添加</el-button>
                                </el-form>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
        </div>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import cancel from '../../static/cancel.png';
export default {
    name:'register2',
    data(){
    //      var checknumber = (rule, value, callback) => {
    //     if (!value) {
    //       return callback(new Error('电话不能为空'));
    //     }
    //     setTimeout(() => {
    //         if (!Number.isInteger(value)) {
    //             callback(new Error('请输入数字值'));
    //         }else if(value < 10000000000){
    //                 callback(new Error('请输入11位数字的电话号码'));
    //         }else  if(value > 100000000000){
    //             callback(new Error('请输入11位数字的电话号码'));
    //         }else{
    //             callback();
    //         }
    //         }, 1000);
    //     };
    //     var checkLevel = (rule, value, callback) => {
    //         if (!value) {
    //         return callback(new Error('等级不能为空'));
    //         }else if(value == '操作管理员' || value == '站点管理员' || value == '公司管理员' || value == '超级管理员' ) {
    //             callback();
    //         }else if (value != '操作管理员' || value != '站点管理员' || value != '公司管理员' || value != '超级管理员' ) {
    //             callback(new Error('请输入正确的内容'));
    //         }
    //     };
    //      var validatePass = (rule, value, callback) => {
    //         if (value === '') {
    //         callback(new Error('请输入密码'));
    //         } else {
    //         if (this.ruleForm2.checkPass !== '') {
    //             this.$refs.ruleForm2.validateField('checkPass');
    //         }
    //         callback();
    //         }
    //     };

    //     var validateaffiliation = (rule, value, callback) => {
    //         if (value === '') {
    //         callback(new Error('请输入用户归属地'));
    //         } else {
    //         callback();
    //         }
    //     };
    //      var validateremark = (rule, value, callback) => {
    //         if (value === '') {
    //         callback(new Error('请输入用户备注'));
    //         } else {
    //         callback();
    //         }
    //     };
    //   var validatePass2 = (rule, value, callback) => {
    //     if (value === '') {
    //       callback(new Error('请再次输入密码'));
    //     } else if (value !== this.ruleForm2.password) {
    //       callback(new Error('两次输入密码不一致!'));
    //     } else {
    //       callback();
    //     }
    //   };
// 站点获取
// http://jenk.gd.cn:8080/FB/site/select


         return{
             cancel,
            ruleForm2: {
                uid:window.localStorage.getItem('uid'),
                devicename:"",
                attribute:"",
                temperature:"",
                normal:"",
                remark:"",
                abnormal_time:"",
                repair_time:"",
                abnormal_cause:"",
                image:"",
                e_site_name: "",
            }
        }
    },
     methods: {
         cancel3(){
                this.$emit('closeparent4000');
         },
      submitForm(formName) {
           this.$confirm('是否确认添加管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '确认添加',
                cancelButtonText: '继续添加'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '请等待...',
                });
             this.$refs[formName].validate((valid) => {
                if (valid) {
                    var url = "http://jenk.gd.cn:8080/FB/equipment/add";
                    var _this = this;
                    var obj = {};
                    obj.devicename = _this.ruleForm2.devicename;
                    obj.attribute = _this.ruleForm2.attribute;
                    obj.temperature = _this.ruleForm2.temperature;
                    obj.uid = _this.ruleForm2.uid;
                    obj.normal = _this.ruleForm2.normal;
                    obj.remark = _this.ruleForm2.remark; 
                    obj.abnormal_time = _this.ruleForm2.abnormal_time.toLocaleString().replace(/年|月|日/g, "").replace(/上午|下午|晚上|凌晨|午夜|早上|清晨/g, " "); 
                    obj.repair_time = _this.ruleForm2.repair_time.toLocaleString().replace(/年|月|日/g, "").replace(/上午|下午|晚上|凌晨|午夜|早上|清晨/g, " "); 
                    obj.abnormal_cause = _this.ruleForm2.abnormal_cause; 
                    obj.image = _this.ruleForm2.image; 
                    obj.e_site_name = _this.ruleForm2.e_site_name;  
                    ajax.inAjaxpost(url,obj,(data)=>{
                        if(data.code == 108){
                            alert('失败');
                            return false;
                        }
                        if(data.code == 200){
                            alert('添加成功');
                            _this.ruleForm2.devicename = '';
                               _this.ruleForm2.temperature = '';
                            _this.ruleForm2.attribute = '';
                            _this.ruleForm2.normal = '';
                            _this.ruleForm2.abnormal_time = ''; 
                            _this.ruleForm2.remark= '';
                            _this.ruleForm2.repair_time= '';
                            _this.ruleForm2.image= '';
                            _this.ruleForm2.e_site_name= '';
                            _this.ruleForm2.abnormal_cause= '';
                            this.$emit('closeparent5000');
                        }
                    })
                } 
                })
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续添加'
                    : '停留在当前页面'
                })
            });
       
      }
     }
}
</script>
<style scoped>
.changstyle1{
    position: relative;
    box-sizing: border-box;
     border: 2px solid #000;
}

.changstyle2{
    font-size: 1.6rem;
    padding: 0.8rem;
    color:rgb(10, 10, 10);
    font-weight: bolder;
}
.el-button--primary{
    font-size: 1.2rem;
    width:80%;
    margin-bottom: 1rem;
}
.el-form{
    width:88%;
    margin:0 auto;
}
.el-form-item {
    margin-bottom: 0px !important;
}
</style>




