<template>
    <div>
        <el-container>
            <img src="../../static/loginBackground.jpg" style="position:absolute;z-index:-1000; height:100vh;width:100vw;" alt="">
            <el-main >
                <el-row >
                    <el-col :span="12">
                        <div class="mask">
                            <div class="changstyle1">
                                <el-row>
                                    <el-col :span="24" style="text-align:left;padding-top:1rem;"><img src="../../static/logo.jpg" alt="用户名" width="186rem"></el-col>
                                    <el-col :span="24"><div class="changstyle2">油田助手后台管理</div></el-col>
                                </el-row>
                                <el-form  :model="ruleForm" :rules="rules" ref="ruleForm"  >
                                    <el-form-item label="" prop="username">
                                            <el-input placeholder="请输入用户名" v-model="ruleForm.username" class="changstyle3">
                                                <template slot="prepend"><img src="../../static/user.png" alt="用户名" width="26rem"></template>
                                            </el-input>
                                    </el-form-item>
                                    <el-form-item label="" prop="password">
                                            <el-input type="password" placeholder="请输入用户密码" v-model="ruleForm.password" class="changstyle3" >
                                                <template slot="prepend"><img src="../../static/passw.png" alt="密码" width="26rem"></template>
                                            </el-input>
                                    </el-form-item>
                                </el-form>
                                <el-row >
                                    <el-col  :span="24" style="text-align:left;height:2rem;" >
                                        <el-checkbox  v-model="checked">记住用户名和密码</el-checkbox>
                                        <!-- <router-link  :to="{ name: 'register'}"><el-button type="text" style="color:#000;padding:0rem;" >立即注册  &nbsp;|</el-button></router-link>
                                        <router-link  :to="{ name: 'forgetPassword'}"><el-button type="text" style="color:#000;padding:0rem;">忘记密码 ？</el-button></router-link> -->
                                    </el-col>
                                </el-row>
                                <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import md5 from 'js-md5';
export default {
    name:'login',
    data(){
        return{
            checked:true,
            checked1:false,
            ruleForm: {
                username:"",
                password: '',
            },
            rules: {
                username: [
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                    { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '请输入用户密码', trigger: 'blur' },
                    { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
                ]
            }
        }
    },
     methods: {
   
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var url = "http://jenk.gd.cn:8080/FB/user/login";
            var _this = this;
                
             let Base64 = require('js-base64').Base64;
              _this.ruleForm.password = Base64.encode(_this.ruleForm.password)
            var obj = _this.ruleForm;
            ajax.inAjaxpost(url,obj,(data)=>{
                if(data.code == 108){
                     alert('登录失败');
                     return false;
                }
                if(data.code == 200){
                    // 登陆成功的时候，登录状态为1的时候就是登录成功
                    window.sessionStorage.setItem('levelJudge',data.level);
                    window.sessionStorage.setItem('loginstatus',1);
                    // window.sessionStorage.setItem('key',data.id);
                    window.sessionStorage.setItem('username',obj.username);
                       window.localStorage.setItem('uid',data.id);
                    window.localStorage.setItem('saveusername',_this.ruleForm.username);
                        if(_this.checked == false){
                                 window.localStorage.setItem('saveusername',"");
                                _this.ruleForm.username = window.localStorage.getItem('saveusername') 
                        }
                    this.$router.push({
                        name: 'backstage',
                    })    
                    return false;                 
                }
            })
          } 
        });
      },
    },
    
    mounted(){
        if(this.checked == true){
            this.ruleForm.username = window.localStorage.getItem('saveusername') 
        }
    }
}
</script>
<style scoped>
.el-main{
    display: flex;
    justify-content: center;
    align-items: center;
    height:100vh;
    width:100vw;
    position: absolute;
}
.mask{
    box-sizing: border-box;
    width:36rem;
    background-color:#fff;
    border-radius:0.6rem;
    margin:0rem auto;
    padding:0 1.6rem;
}
.changstyle2{
    font-size: 1.6rem;
    padding-bottom: 1rem;
    color:rgb(1,143,245);
}
.el-button--primary{
    font-size: 1.2rem;
    width:100%;
    margin-bottom: 2rem ;
    border-radius: 28px;
}
.el-form{
    width:88%;
    margin:0 auto;
}
.el-form-item{
    margin-bottom: 35px;
}
</style>



