<template>
    <div>
        <div style="position: absolute;top:0;left:0;bottom:0px;right:0px; background-color:#333;opacity: 0.5;z-index: 1;"></div>
        <div style="position: absolute;width: 100vw;z-index: 2;">
        <el-container>
            <el-main >
                <el-row >
                    <el-col style="background-color:#fff;margin-top: 4rem;" :span="12" :offset="6">
                        <div >
                            <div class="changstyle1">
                                 <div style="width:18px;height:18px;position:absolute;top:0rem;right:0.5rem;z-index:111">
                                          <el-button style="border:0px solid #fff;padding:0rem;" @click="cancel3">
                                                <img :src="cancel" alt="取消" width='25px'>
                                          </el-button>
                                </div>
                                <el-row>
                                    <el-col :span="24"><div class="changstyle2">修改</div></el-col>
                                </el-row>
                               <el-form :model="ruleForm3" status-icon  :rules="rules3" ref="ruleForm3" label-width="6rem" >
                                      <el-form-item label="站点名" prop="e_site_name">
                                        <el-input   placeholder="请输入站点名" v-model="ruleForm3.e_site_name"></el-input>
                                    </el-form-item>
                                    <el-form-item  label="检查设备" prop="devicename">
                                        <el-input placeholder="请输入检查设备" v-model="ruleForm3.devicename"></el-input>
                                    </el-form-item>
                                      <el-form-item  label="温度" prop="temperature">
                                        <el-input placeholder="请输入温度" v-model="ruleForm3.temperature"></el-input>
                                    </el-form-item>
                                     <el-form-item label="设备属性" prop="attribute">
                                        <el-input  placeholder="请输入设备属性" v-model="ruleForm3.attribute" ></el-input>
                                    </el-form-item>
                                     <el-form-item label="设备状态" prop="normal">
                                                <el-input placeholder="请输入设备状态" v-model="ruleForm3.normal"></el-input>
                                    </el-form-item>
                                    <el-form-item label="异常时间" prop="abnormal_time">
                                                <el-date-picker  v-model="ruleForm3.abnormal_time" type="datetime" placeholder="选择异常日期时间"></el-date-picker>
                                    </el-form-item>
                                     <el-form-item label="异常原因" prop="abnormal_cause">
                                        <el-input placeholder="请输入异常原因" v-model="ruleForm3.abnormal_cause"></el-input>
                                    </el-form-item> 
                                     <el-form-item label="修复时间" prop="repair_time">
                                              <el-date-picker  v-model="ruleForm3.repair_time" type="datetime"  placeholder="选择修复日期时间"> </el-date-picker>
                                    </el-form-item>
                                       <el-form-item label="备注" prop="remark">
                                        <el-input placeholder="请输入备注" v-model="ruleForm3.remark"></el-input>
                                    </el-form-item>
                                       <el-form-item label="图片" prop="image">
                                        <el-input  placeholder="请输入图片" v-model="ruleForm3.image" ></el-input>
                                    </el-form-item>
                                        <el-button type="primary" @click="submitForm('ruleForm3')">修改</el-button>
                                </el-form>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
        </div>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import cancel from '../../static/cancel.png';
export default {
       inject:['reload'],
    name:'modification1',
    data(){
         return{
                  cancel,
                ruleForm3: {
                        uid:window.localStorage.getItem('uid'),
                        devicename:window.sessionStorage.getItem("devicename"),
                        id:window.sessionStorage.getItem("id"),
                        attribute: window.sessionStorage.getItem("attribute"),
                        normal:window.sessionStorage.getItem("normal"),
                        repair_time:window.sessionStorage.getItem("repair_time"),
                        remark:window.sessionStorage.getItem("remark"),
                        abnormal_time:window.sessionStorage.getItem("abnormal_time"),
                        abnormal_cause:window.sessionStorage.getItem("abnormal_cause"),
                        image:window.sessionStorage.getItem("image"),
                        e_site_name:window.sessionStorage.getItem("e_site_name"),
                        temperature:window.sessionStorage.getItem("temperature"),
                        },
                     rules3: {
                        remark: [
                            { message: '请输入用户备注', trigger: 'blur' }
                        ],
                    }
                }
    },
     methods: {
         cancel3(){
             this.$emit("closeparent4")
         },
      submitForm(formName) {
           this.$confirm('是否确认修改管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '确认修改',
                cancelButtonText: '继续修改'
            })
             .then(() => {
                this.$message({
                type: 'info',
                message: '已经修改',
                });
                   this.$refs[formName].validate((valid) => {
                    if (valid) {
                    var url = "http://jenk.gd.cn:8080/FB/equipment/update";
                    var _this = this;
                  var obj = {};
                    obj.e_site_name = _this.ruleForm3.e_site_name;
                    obj.image = _this.ruleForm3.image;
                   obj.uid = _this.ruleForm3.uid;
                        obj.id = _this.ruleForm3.id;
                        obj.devicename = _this.ruleForm3.devicename;
                           obj.temperature = _this.ruleForm3.temperature;
                        obj.attribute = _this.ruleForm3.attribute;
                        obj.normal = _this.ruleForm3.normal;
                        obj.repair_time = _this.ruleForm3.repair_time.toLocaleString().replace(/年|月|日/g, "").replace(/上午|下午|晚上|凌晨|午夜|早上|清晨/g, " ");
                        obj.remark = _this.ruleForm3.remark; 
                        obj.abnormal_time = _this.ruleForm3.abnormal_time.toLocaleString().replace(/年|月|日/g, "").replace(/上午|下午|晚上|凌晨|午夜|早上|清晨/g, " ");
                        obj.abnormal_cause = _this.ruleForm3.abnormal_cause;   
                  ajax.inAjaxpost(url,obj,(data)=>{
                        if(data.code == 108){
                            console.log(obj);
                            alert('修改失败');
                            return false;
                        }
                         if(data.code == 200){
                             _this.$emit('closeparent5')
                           
                        }
                    })
                } 
                })
            })
           .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续修改'
                    : '停留在当前页面'
                })
            });
        
      }
     }
}
</script>
<style scoped>
.changstyle1{
    position: relative;
    box-sizing: border-box;
     border: 2px solid #000;
}
.changstyle2{
    font-size: 1.6rem;
    padding: 1.5rem;
    color:rgb(10, 10, 10);
    font-weight: bolder;
}
.el-button--primary{
    font-size: 1.2rem;
    width:80%;
    margin: 1rem 0;
}
.el-form{
    width:88%;
    margin:0 auto;
}
.el-form-item {
    margin-bottom: 0px !important;
}
</style>




