/* 仓库文件 */
import Vue from 'vue'
import Vuex from 'vuex'
 
Vue.use(Vuex);
//一般通过外部文件引入的。
const state = { 
  //定义数据 --- 类似data
  num: 666,  //生成了一个静态的常量
  status: true, // 弹窗的显示和隐藏  false隐藏，true显示
}
// mutation 都是同步函数
const mutations = {
  //定义方法 --- 类似methods
  //state 是把上面的数据引入进来的
  showorclosefn (state,type) {//可以传递参数 store.commit('showorclosefn', 10)
    console.log(state)
    state.status = !state.status;
  },
  closefn (status) {
    state.status = false;
  },
}
// actions 都是异步函数
const actions = {
  showorclosefn (context) {
    context.commit('showorclosefn')
  }
}
 
export default new Vuex.Store({
  state,
  mutations
})



