<template>
    <div >
            <el-row style="margin:0 1rem;"><el-col :span="4" :offset="20" style="text-align:right;">
                        <el-button type="primary"  size="mini" @click="open100" style="margin-bottom:1rem; padding:0.8rem 2.6rem;">添加</el-button>
                    </el-col>  
                </el-row>
            <el-table
                :data="tableData"
                border
                style="width: 100%">
                  <el-table-column
                prop="s_site_name"
                label="站点名"
                >
                </el-table-column>
                <el-table-column
                prop="s_name"
                label="取样点名"
                >
                </el-table-column>
                <el-table-column
                prop="s_pid"
                label="pid位"
                >
                </el-table-column>
                <el-table-column
                prop="s_temperature"
                label="取样点温度"
                >
                </el-table-column>
                <el-table-column
                prop="s_pressure"
                label="压力"
                >
                </el-table-column>
                 <el-table-column
                prop="s_flow"
                label="流量"
                >
                </el-table-column>
                 <el-table-column
                prop="s_water"
                label="含水率"
                >
                </el-table-column>
                <el-table-column
                prop="s_gas"
                label="气液比"
                >
                </el-table-column>
                <el-table-column
                prop="s_medicament"
                label="化学药剂"
                >
                </el-table-column>

                 <el-table-column
                prop="s_injectivity"
                label="注射量"
                >
                </el-table-column>
             
                 <el-table-column
                prop="s_carbon"
                label="二氧化碳含量"
                >
                </el-table-column>
                 <el-table-column
                prop="s_sulfide"
                label="硫化氢"
                >
                </el-table-column>
                  <el-table-column
                prop="s_image"
                label="图片"
                >
                </el-table-column>
                <el-table-column
                label="操作"
                >
                   <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small" >修改</el-button> | <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">移除</el-button>
                    </template>
                </el-table-column>
            </el-table>
             <div class="block" style="text-align:center;margin-top:1rem;">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentpage"
                :page-size="pageSize"
                layout="prev, pager, next"
                :total="talee">
                </el-pagination>
            </div>
            <div v-if="show50" style="width:100vw;height:100vh;position: fixed;top:0;left:0;z-index: 2;">
                <modification3 @closeparent210='closeparent210' @closeparent2000='closeparent2000'></modification3>
            </div>
              <!-- 添加 -->
            <div v-if="show200" style="width:100vw;height:100vh;position: fixed;top:0;left:0;z-index: 2;">
                <register1 @closeparent100s='closeparent100s' @closeparent300="closeparent300"></register1>
            </div>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import modification3 from './modification3';
import register1 from './register1';
export default {
    name:'sampling',
    inject:['reload'],
    props:{
            tableD200:''
    },
// 增加：增加：http://jenk.gd.cn:8080/FB/sampling/add
// 传：uid,s_name, s_pid,  s_temperature,  s_pressure,  s_flow, s_water,  s_gas,  s_medicament,  s_injectivity,  s_carbon,  s_sulfide,s_site_name,  s_image
// post方式

// 删除：删除：http://jenk.gd.cn:8080/FB/sampling/delete
// 传：uid,s_id
// post方式

// 修改：修改：http://jenk.gd.cn:8080/sampling/update
// 传：uid,s_id,s_name, s_pid,  s_temperature,  s_pressure,  s_flow, s_water,  s_gas,  s_medicament,  s_injectivity,  s_carbon,  s_sulfide,s_site_name,  s_image
// post方式

// 查询所有：所有：http://jenk.gd.cn:8080/FB/sampling/selectAll
// 传：uid
// post方式
    data(){
         return{
            show200:false,
            show50:false,
            pageSize:5,
            currentpage:1,
            tableDatas:[],
            tableData:[],
            talee:1,
            perval:5,
            userSelects:{
                    uid:window.localStorage.getItem('uid'),
                },
        }
    },
  methods: {
      extrats(){
            var url = "http://jenk.gd.cn:8080/FB/sampling/selectAll";
            var _this = this;
            var obj = _this.userSelects;
        ajax.inAjaxpost(url,obj,(data)=>{
             var _this = this;
                    _this.tableData = data.sampling.slice(0,_this.val);
                    _this.tableDatas = data.sampling;
                     _this.currentpage = 1;
                    _this.talee = data.sampling.length;
        });
      },
      handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.tableData=this.tableDatas.slice((val-1)*this.pageSize,val*this.pageSize)
        },
         closeparent300(){
           this.$confirm('是否添加管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '取消添加',
                cancelButtonText: '继续添加'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '取消添加',
                });
                this.show200 = false;
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续添加'
                    : '停留在当前页面'
                })
            });
         },
         closeparent2000(){
             this.$confirm('是否修改管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '取消修改',
                cancelButtonText: '继续修改'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '取消修改',
                });
                this.show50 = false;
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续修改'
                    : '停留在当前页面'
                })
            });
         },
         closeparent100s(){
            this.show200 = false;
                    this.reload;
                    this.extrats();
                    this.tableData=this.tableDatas.slice(0,this.perval)
         },
         closeparent210(){
                this.show50= false;
                  this.reload;
                    this.extrats();
                    this.tableData=this.tableDatas.slice(0,this.perval)
                    
         },
        open100(){
            this.show200 = true;
      },
      handleClick(row) {
            this.show50= true;
            window.sessionStorage.setItem("s_id",row.s_id);
            window.sessionStorage.setItem("s_name",row.s_name);
            window.sessionStorage.setItem("s_pid",row.s_pid);
            window.sessionStorage.setItem("s_temperature",row.s_temperature);
            window.sessionStorage.setItem("s_pressure",row.s_pressure);
            window.sessionStorage.setItem("s_flow",row.s_flow);
            window.sessionStorage.setItem("s_water",row.s_water);
            window.sessionStorage.setItem("s_gas",row.s_gas);
            window.sessionStorage.setItem("s_medicament",row.s_medicament);
            window.sessionStorage.setItem("s_injectivity",row.s_injectivity);
            window.sessionStorage.setItem("s_carbon",row.s_carbon);
            window.sessionStorage.setItem("s_sulfide",row.s_sulfide);
            window.sessionStorage.setItem("s_site_name",row.s_site_name);
            window.sessionStorage.setItem("s_image",row.s_image);
      },
       deleteRow(index, rows) {
        //    删除当前的数据
        this.$confirm('是否确认删除？', '确认信息', {
            distinguishCancelAndClose: true,
            confirmButtonText: '确认删除',
            cancelButtonText: '取消删除'
            })
          .then(() => {
            this.$message({
              type: 'info',
              message: '已经删除'
            });
             var url = "http://jenk.gd.cn:8080/FB/sampling/delete";
             var obj  = {};
             var _this = this;
                obj.s_id =  rows[index].s_id;
                obj.uid = _this.userSelects.uid;
                ajax.inAjaxpost(url,obj,(data)=>{
                });
                    this.reload;
                    this.extrats();
                    this.currentpage = 1;
                    this.tableData=this.tableDatas.slice(0,this.perval)
          })
          .catch(action => {
            this.$message({
              type: 'info',
              message: action === 'cancel'
                ? '取消删除'
                : '停留在当前页面'
            })
          });
      }
    },
     components:{
      modification3,
      register1
  },
  watch: {
        tableD200: function (vals) {
            var _this = this;
                    _this.tableData = vals.slice(0,_this.val);
                    _this.tableDatas = vals;
                     _this.currentpage = 1;
                    _this.talee = vals.length;
        }
    },
}
</script>
<style scoped>
    .el-button+.el-button {
    margin-left: 0px;
}
.el-form-item {
    margin-bottom: 0px !important;
}
</style>




