<template>
    <div>
                <el-row>
                    <el-col style="text-align:right; margin-top:2rem;">
                            <el-button  class="changestyle7" size="mini" @click="exit11">返回</el-button>
                    </el-col>
                </el-row>
                 <el-row style="margin:0 1rem;"><el-col style="font-size:2rem;margin-top:2rem;">
                       我的个人信息
                    </el-col>  
                </el-row>
            <el-col :offset="6" :span="12" style="margin-top:3rem;border:1px solid #000;">
                    <el-col>
                        <el-card shadow="hover">
                          <el-col :span="6" style="text-align:right;"> 管理员 :</el-col> {{loginUsername.username}}
                        </el-card>
                    </el-col>
                    <el-col>
                        <el-card shadow="hover">
                        <el-col :span="6" style="text-align:right;">密码 : </el-col>{{loginUsername.password}}
                        </el-card>
                    </el-col>
                    <el-col>
                        <el-card shadow="hover">
                            <el-col :span="6" style="text-align:right;">  电话 : </el-col>{{loginUsername.telphone}}
                        </el-card>
                    </el-col>
                    <el-col>
                        <el-card shadow="hover">
                            <el-col :span="6" style="text-align:right;">备注 :</el-col>{{loginUsername.remark}}
                        </el-card>
                    </el-col>
                    <el-col>
                        <el-card shadow="hover">
                            <el-col :span="6" style="text-align:right;">账号归属地 :</el-col>{{loginUsername.affiliation}}
                        </el-card>
                    </el-col>
                    <el-col>
                        <el-card shadow="hover">
                            <el-col :span="6" style="text-align:right;">等级 :</el-col>{{loginUsername.level}}
                        </el-card>
                    </el-col>
                    <el-col>
                        <el-card shadow="hover">
                            <el-col :span="6" style="text-align:right;">是否登录 :</el-col>{{loginUsername.login}}
                        </el-card>
                    </el-col>
            </el-col>
              <el-row style="margin:0 1rem;"><el-col  style="margin-top:3rem;">
                        <el-button type="primary"  size="mini" @click="open1" style="margin-bottom:1rem; padding:0.8rem 2.6rem;">修改</el-button>
                    </el-col>  
                </el-row>
       <div v-if="show1" style="width:100vw;height:100vh;position: fixed;top:0;left:0;z-index: 2;">
                <modification2   @closeparent20='closeparent20' @closeparent21='closeparent21' :loginUsername='loginUsername'></modification2>
            </div>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import modification2 from './modification2';
export default {
   name:'revealUser',
    data(){
        return{
            show1:false,
            loginUsername:{},
                userSelects:{
         
         
                    uid:window.localStorage.getItem('uid'),
                    },
        }
    },
    mounted(){
            var url = "http://jenk.gd.cn:8080/FB/user/select";
                var obj = {};
                var _this = this;
                obj = _this.userSelects;
                ajax.inAjaxpost(url,obj,(data)=>{
                        for(var i = 0 ; i < data.user.length ; i++){
                            if(data.user[i].level == '1') {
                                data.user[i].level = '操作管理员';
                            }
                            else  if( data.user[i].level == '2') {
                                data.user[i].level = '站点管理员';
                            
                            }else  if( data.user[i].level == '3') {
                                data.user[i].level = '公司管理员';
                            
                            } else  if( data.user[i].level == '4') {
                                data.user[i].level = '超级管理员';
                            }
                            else{
                            }
                        }
                    for(var i = 0 ; i < data.user.length ; i++){
                            if(data.user[i].username  ==  window.localStorage.getItem('saveusername')){
                           _this.loginUsername = data.user.splice(i,1)[0]
                            }
                    }
                })
    },
    methods:{
     exit11(){
         this.$router.push({
             name:'backstage'
         });
     },
        closeparent20(){
             this.$confirm('是否修改管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '取消修改',
                cancelButtonText: '继续修改'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '取消修改',
                });
                this.show1 = false;
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续修改'
                    : '停留在当前页面'
                })
            });
         },
          closeparent21(){
                this.show1 = false;
         },
         open1() {
          this.show1 = true;
        },
    },
     components:{
      modification2,
    },
}


</script>
<style scoped>
.changestyle7{
   
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    border: 1px solid rgb(1,143,245);
   color:red !important;
    -webkit-appearance: none;
    text-align: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    outline: 0;
    margin: 0;
    -webkit-transition: .1s;
    transition: .1s;
    font-weight: 500;
    padding:0.2rem 1.1rem !important;
    margin-right:1.5rem !important;
    margin-bottom: 1rem !important;
    font-size: 17px !important;
    border-radius: 4px !important;
}
</style>



