<template>
    <div >
         <el-row :gutter="20" >
            <el-col :span="10" :offset="11"><div style="line-height:28px;height:28px;text-align:right;color:rgb(234, 99, 31);"></div></el-col>
            </el-row>
            
            <el-table
                :data="tableData"
                border
                style="width: 100%">
                <el-table-column
                prop="id"
                label="ID"
                >
                </el-table-column>
                <el-table-column
                prop="content"
                label="操作内容"
                >
                </el-table-column>
                <el-table-column
                prop="admin"
                label="管理员"
                >
                </el-table-column>
                <el-table-column
                prop="createDate"
                label="操作时间"
                >
                  </el-table-column>
                <el-table-column
                label="操作"
                >
                   <template slot-scope="scope">
                       <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">移除</el-button>
                    </template>
                </el-table-column>
            </el-table>
                <div class="block" style="text-align:center;margin-top:1rem;">
                    <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentpage"
                    :page-size="pageSize"
                    layout="prev, pager, next"
                    :total="talee">
                    </el-pagination>
                </div>
    </div>
</template>
<script>
import ajax from '../common/ajax';
export default {
        name:'maintain',
     inject:['reload'],
        props:{
                tableD3:''
        },
        data(){
         return{
            pageSize:6,
            tableDatas:[],
            tableData:[],
            talee:1,
            currentpage:1,
            perval:6,
        }
    },
    
  methods: {
      extrats(){
        var url = "http://jenk.gd.cn:8080/FB/record/select";
         var _this = this;
         var obj = {};
            ajax.inAjaxpost(url,obj,(data)=>{
            for(var i = 0 ; i < data.equipment.length ; i++){
                    _this.tableData = data.equipment.slice(0,_this.perval);
                    _this.currentpage = 1;
                    _this.tableDatas = data.equipment;
                    _this.talee = _this.tableDatas.length;
            }
        });
      },
      handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.tableData=this.tableDatas.slice((val-1)*this.pageSize,val*this.pageSize)
        },
       deleteRow(index, rows) {
        //    删除当前的数据
        this.$confirm('是否确认删除？', '确认信息', {
            distinguishCancelAndClose: true,
            confirmButtonText: '确认删除',
            cancelButtonText: '取消删除'
            })
          .then(() => {
            this.$message({
              type: 'info',
              message: '已经删除'
            });
             var url = "http://jenk.gd.cn:8080/FB/record/delete";
                var obj =  rows.splice(index, 1)[0];
                ajax.inAjaxpost(url,obj,(data)=>{
                });
                  this.reload;
                    this.extrats();
                      this.currentpage = 1;
                       this.tableData=this.tableDatas.slice(0,this.perval)
          })
          .catch(action => {
            this.$message({
              type: 'info',
              message: action === 'cancel'
                ? '取消删除'
                : '停留在当前页面'
            })
          });
      }
    },
  watch: {
        tableD3: function (vals) {
            var _this = this;
             for(var i = 0 ; i < vals.length ; i++){
                    _this.tableData = vals.slice(0,_this.perval);
                    _this.tableDatas = vals;
                       _this.currentpage = 1;
                    _this.talee = vals.length;
            }
        }
    },
}
</script>
<style scoped>
    .el-button+.el-button {
    margin-left: 0px;
}
</style>




