<template>
    <div>
        <div style="position: absolute;top:0;left:0;bottom:0px;right:0px; background-color:#333;opacity: 0.5;z-index: 1;"></div>
        <div style="position: absolute;width: 100vw;z-index: 2;">
        <el-container>
                           
            <el-main >
                <el-row >
                    <el-col style="background-color:#fff;margin-top: 4rem;" :span="12" :offset="6">
                        <div >
                            <div class="changstyle1">
                                 <div style="width:18px;height:18px;position:absolute;top:0rem;right:0.5rem;z-index:111">
                                          <el-button style="border:0px solid #fff;padding:0rem;" @click="cancel2">
                                                <img :src="cancel" alt="取消" width='25px'>
                                          </el-button>
                                </div>
                                <el-row>
                                    <el-col :span="24"><div class="changstyle2">修改</div></el-col>
                                </el-row>
                               <el-form :model="loginUsername" status-icon :rules="rules2" ref="loginUsername" label-width="6rem" >
                                    <el-form-item  label="用户名" prop="username">
                                        <el-input placeholder="请输入用户名" v-model="loginUsername.username"></el-input>
                                    </el-form-item>
                                    <el-form-item label="密码" prop="password">
                                        <el-input type="password" placeholder="请输入用户密码" v-model="loginUsername.password" autocomplete="off"></el-input>
                                    </el-form-item>
                                    <el-form-item label="电话" prop="telphone">
                                        <el-input  type="tel" placeholder="请输入用户电话" v-model.number=" loginUsername.telphone"></el-input>
                                    </el-form-item>
                                   <el-form-item label="备注" prop="remark">
                                        <el-input placeholder="请输入用户备注" v-model="loginUsername.remark"></el-input>
                                    </el-form-item>
                                    <el-form-item label="等级" prop="level">
                                        <el-input placeholder="请输入用户等级（1操作管理员,2站点管理员,3公司管理员,4超级管理员）" v-model="loginUsername.level"></el-input>
                                    </el-form-item>
                                     <el-form-item label="账号归属地" prop="affiliation">
                                        <el-input placeholder="请输入用户归属地:  X X 市" v-model="loginUsername.affiliation"></el-input>
                                    </el-form-item> 
                                    <el-form-item label="是否登录" prop="login" style=" margin-bottom: 0px;">
                                            <el-radio v-model="loginUsername.login" label="是">是</el-radio>
                                        <el-radio v-model="loginUsername.login" label="否">否</el-radio>
                                    </el-form-item>
                                        <el-button type="primary" @click="submitForm('loginUsername')">修改</el-button>
                                </el-form>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
        </div>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import cancel from '../../static/cancel.png';
export default {
      
    name:'modification2',
    props:{
            loginUsername:''
    },
    data(){
         var checknumber = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('电话不能为空'));
            }
        setTimeout(() => {
            if (!Number.isInteger(parseInt(value) )) {
                callback(new Error('请输入数字值'));
            }else if(value < 10000000000){
                    callback(new Error('请输入11位数字的电话号码'));
            }else  if(value > 100000000000){
                callback(new Error('请输入11位数字的电话号码'));
            }else{
                callback();
            }
            }, 1000);
        };
        var checkLevel = (rule, value, callback) => {
            if (!value) {
            return callback(new Error('等级不能为空'));
            }else if(value == '操作管理员' || value == '站点管理员' || value == '公司管理员' || value == '超级管理员' ) {
                callback();
            }else if (value != '操作管理员' || value != '站点管理员' || value != '公司管理员' || value != '超级管理员' ) {
                callback(new Error('请输入正确的内容'));
            }
        };
         var validatePass = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('请输入密码'));
            } else {
            callback();
            }
        };
         var validateaffiliation = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('请输入用户归属地'));
            } else {
            callback();
            }
        };
         var validateremark = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('请输入用户备注'));
            } else {
            callback();
            }
        };
         return{
                  cancel,

            rules2: {
                password: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                username: [
                    { message: '请输入用户名', trigger: 'blur' },
                ],
                remark: [
                    { validator: validateremark, trigger: 'blur' },
                    { mix:1, max:50,message: '请输入用户备注1-50个字符', trigger: 'blur' }
                ],
                telphone: [
                    { validator: checknumber, trigger: 'blur' },
                ],
                level: [
                    { validator: checkLevel, trigger: 'blur' },
                ],
             
                affiliation: [
                    { validator: validateaffiliation, trigger: 'blur' },
                    { mix:1, max:50,message: '请输入用户归属地', trigger: 'blur' }
                ],
            }
        }
    },
     methods: {
         cancel2(){
             this.$emit("closeparent20")
         },
      submitForm(formName) {
           this.$confirm('是否确认修改管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '确认修改',
                cancelButtonText: '继续修改'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '已经修改',
                });
                   this.$refs[formName].validate((valid) => {
                    if (valid) {
                    var url = "http://jenk.gd.cn:8080/FB/user/update";
                    let Base64 = require('js-base64').Base64;
                    this.loginUsername.password = Base64.encode(this.loginUsername.password);
                    var obj = {};
                         obj.uid = this.loginUsername.id;
                    obj.id = this.loginUsername.id;
                    obj.password = this.loginUsername.password;
                    obj.telphone =this.loginUsername.telphone ;
                    obj.remark = this.loginUsername.remark;
                    obj.username = this.loginUsername.username;
                    obj.affiliation = this.loginUsername.affiliation; 
                    obj.login = this.loginUsername.login;  
                    if( this.loginUsername.level == '操作管理员') {
                        obj.level = 1 
                    }
                    else if(  this.loginUsername.level == '站点管理员') {
                        obj.level = 2 
                    }
                    else if(  this.loginUsername.level == '公司管理员') {
                        obj.level = 3 
                    }
                    else if(  this.loginUsername.level == '超级管理员') {
                        obj.level= 4 
                    }
                    ajax.inAjaxpost(url,obj,(data)=>{
                        if(data.code == 108){
                            alert('修改失败');
                            return false;
                        }
                        if(data.code == 200){
                            window.localStorage.setItem("saveusername" , obj.username );
                             this.$emit("closeparent21")
                        }
                    })
                } 
                })
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续修改'
                    : '停留在当前页面'
                })
            });
        
      }
     },

}
</script>
<style scoped>
.changstyle1{
    position: relative;
    box-sizing: border-box;
     border: 2px solid #000;
}
.changstyle2{
    font-size: 1.6rem;
    padding: 1.5rem;
    color:rgb(10, 10, 10);
    font-weight: bolder;
}
.el-button--primary{
    font-size: 1.2rem;
    width:80%;
    margin: 1rem 0;
}
.el-form{
    width:88%;
    margin:0 auto;
}
</style>




