<template>
    <div >
           <el-row style="margin:0 1rem;"><el-col :span="4" :offset="20" style="text-align:right;">
                        <el-button type="primary"  size="mini" @click="open9" style="margin-bottom:1rem; padding:0.8rem 2.6rem;">添加</el-button>
                    </el-col>  
                </el-row>
            <el-table
                :data="tableData"
                border
                style="width: 100%">
                <el-table-column
                prop="id"
                label="ID"
                >
                </el-table-column>
                  <el-table-column
                prop="e_site_name"
                label="站点名"
                >
                </el-table-column>
                <el-table-column
                prop="devicename"
                label="检查设备"
                >
                </el-table-column>
                <el-table-column
                prop="attribute"
                label="设备属性"
                >
                </el-table-column>
                <el-table-column
                prop="normal"
                label="设备状态"
                >
                </el-table-column>
                 <el-table-column
                prop="remark"
                label="备注"
                >
                </el-table-column>
                 <el-table-column
                prop="abnormal_time"
                label="异常时间"
                >
                </el-table-column>
                  <el-table-column
                prop="temperature"
                label="温度"
                >
                </el-table-column>
                <el-table-column
                prop="repair_time"
                label="修复时间"
                >
                </el-table-column>
                <el-table-column
                prop="abnormal_cause"
                label="异常原因"
                >
                </el-table-column>
              
                     <el-table-column
                prop="image"
                label="图片"
                >
                </el-table-column>
                <el-table-column
                label="操作"
                >
                   <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small" >修改</el-button> | <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">移除</el-button>
                    </template>
                </el-table-column>
            </el-table>
             <div class="block" style="text-align:center;margin-top:1rem;">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                    :current-page.sync="currentpage"
                :page-size="pageSize"
                layout="prev, pager, next"
                :total="talee">
                </el-pagination>
            </div>
            <div v-if="show5" style="width:100vw;height:100vh;position: fixed;top:0;left:0;z-index: 2;">
                <modification1 @closeparent5='closeparent5' @closeparent4='closeparent4'></modification1>
            </div>
              <div v-if="show500" style="width:100vw;height:100vh;position: fixed;top:0;left:0;z-index: 2;">
                <register2 @closeparent5000='closeparent5000' @closeparent4000='closeparent4000'></register2>
            </div>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import modification1 from './modification1';
import register2 from './register2';

export default {
   
    name:'management1',
     inject:['reload'],
    props:{
            tableD1:''
    },
    data(){
         return{
            show5:false,
            show500:false,
            pageSize:5,
            tableDatas:[],
            tableData:[],
            talee:1,
               currentpage:1,
             perval:5,
              userSelects:{
                       uid:window.localStorage.getItem('uid'),
                },
        }
    },
    
  methods: {
      open9(){
            this.show500 = true;
      },
      extrats(){
        var url = "http://jenk.gd.cn:8080/FB/equipment/select";
         var _this = this;
            var obj = _this.userSelects;
        ajax.inAjaxpost(url,obj,(data)=>{
                    _this.tableData = data.equipment.slice(0,_this.perval);
                    _this.tableDatas = data.equipment;
                    _this.talee = _this.tableDatas.length;
        });
      },
      handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.tableData=this.tableDatas.slice((val-1)*this.pageSize,val*this.pageSize)
        },
        closeparent4000(){
            this.$confirm('是否添加管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '取消添加',
                cancelButtonText: '继续添加'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '取消添加',
                });
                this.show500 = false;
                   this.extrats();
                this.reload;
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续添加'
                    : '停留在当前页面'
                })
            });
        },
         closeparent4(){
             this.$confirm('是否修改管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '取消修改',
                cancelButtonText: '继续修改'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '取消修改',
                });
                this.show5 = false;
                  this.extrats();
                this.reload;
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续修改'
                    : '停留在当前页面'
                })
            });
         },
         closeparent5000(){
              this.show500 = false;
                     this.reload;
                    this.extrats();
                    this.currentpage = 1;
                    this.tableData=this.tableDatas.slice(0,this.perval)
         },
         closeparent5(){
                    this.show5 = false;
                     this.reload;
                    this.extrats();
                    this.currentpage = 1;
                    this.tableData=this.tableDatas.slice(0,this.perval)
         },
      handleClick(row) {
        //   当前数据row
            this.show5= true;
            window.sessionStorage.setItem("uid",row.id);
            window.sessionStorage.setItem("devicename",row.devicename);
            window.sessionStorage.setItem("id",row.id);
            window.sessionStorage.setItem("attribute",row.attribute);
            window.sessionStorage.setItem("normal",row.normal);
            window.sessionStorage.setItem("repair_time",row.repair_time);
              window.sessionStorage.setItem("temperature",row.temperature)
            window.sessionStorage.setItem("remark",row.remark);
            window.sessionStorage.setItem("abnormal_time",row.abnormal_time);
            window.sessionStorage.setItem("abnormal_cause",row.abnormal_cause);
            window.sessionStorage.setItem("image",row.image);
            window.sessionStorage.setItem("e_site_name",row.e_site_name);
      },
       deleteRow(index, rows) {
        //    删除当前的数据
        this.$confirm('是否确认删除？', '确认信息', {
            distinguishCancelAndClose: true,
            confirmButtonText: '确认删除',
            cancelButtonText: '取消删除'
            })
          .then(() => {
            this.$message({
              type: 'info',
              message: '已经删除'
            });
//             增加：http://jenk.gd.cn:8080/FB/equipment/add
// 传：uid,devicename,attribute,temperature,normal,remark,abnormal_time,repair_time,abnormal_cause,image, e_site_name
// post方式

// 修改：http://jenk.gd.cn:8080/FB/equipment/update
// 传：uid,id,devicename,attribute,temperature,normal,remark,abnormal_time,repair_time,abnormal_cause,image, e_site_name
             var url = "http://jenk.gd.cn:8080/FB/equipment/delete";
               var _this = this;
                var obj1 =  rows.splice(index, 1)[0];
                console.log(obj1);
                var  id =  obj1.id;
                // var  username =  obj1.username;
                // var  password = obj1.password;
                // var  telphone = obj1.telphone;
                var  remark =  obj1.remark;
                var  level = obj1.level;
                // var  affiliation = obj1.affiliation;
                // var  login =  obj1.login;
                var uid = _this.userSelects.uid ;
                var image =  obj1.image ;
                var  obj =   {id,remark , uid,image }
                ajax.inAjaxpost(url,obj,(data)=>{
                });
                  this.reload;
                    this.extrats();
                      this.currentpage = 1;
                       this.tableData=this.tableDatas.slice(0,this.perval)
                  
          })
          .catch(action => {
            this.$message({
              type: 'info',
              message: action === 'cancel'
                ? '取消删除'
                : '停留在当前页面'
            })
          });
      }
    },
     components:{
      modification1,
register2
  },
  watch: {
        tableD1: function (vals) {
            var _this = this;
                    _this.tableData = vals.slice(0,_this.perval);
                    _this.tableDatas = vals;
                       _this.currentpage = 1;
                    _this.talee = vals.length;
        }
    },
}
</script>
<style scoped>
    .el-button+.el-button {
    margin-left: 0px;
}
</style>




