<template>
    <div>
           <div style="position: absolute;top:0;left:0;bottom:0px;right:0px; background-color:#333;opacity: 0.5;z-index: 1;"></div>
        <div style="position: absolute;width: 100vw;z-index: 2;">
        <el-container>
            <el-main style="margin-top:2rem;">
                <el-row >
                    <el-col style="background-color:#fff;" :span="12" :offset="6">
                        <div >
                            <div class="changstyle1" >
                                  <div style="width:18px;height:18px;position:absolute;top:0rem;right:0.5rem;z-index:111">
                                          <el-button style="border:0px solid #fff;padding:0rem;" @click="cancel3">
                                                <img :src="cancel" alt="取消" width='25px'>
                                          </el-button>
                                </div>
                                <el-row>
                                    <el-col :span="24"><div class="changstyle2">添加</div></el-col>
                                </el-row>
                                <el-row>
                                    <el-col :span="24"><div class="changstyle2"></div></el-col>
                                </el-row>
                               <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="6rem" >
                                    <el-form-item style="line-height:-1rem;" label="站点名" prop="site_name">
                                        <el-input placeholder="请输入站点名" v-model="ruleForm2.site_name"></el-input>
                                    </el-form-item>
                                    <el-form-item style="line-height: 1rem!important;" label="取样数量" prop="sampling_num">
                                        <el-input  placeholder="请输入取样数量" v-model.number="ruleForm2.sampling_num" ></el-input>
                                    </el-form-item>
                                   <el-form-item label="设备数量" prop="equipment_num">
                                        <el-input placeholder="请输入设备数量" v-model.number="ruleForm2.equipment_num"></el-input>
                                    </el-form-item>
                                        <el-button type="primary" @click="submitForm('ruleForm2')">添加</el-button>
                                </el-form>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
        </div>
         </div>
</template>
<script>
import ajax from '../common/ajax';
import cancel from '../../static/cancel.png';
export default {
    name:'register',
    data(){
         return{
             cancel,
            ruleForm2: {
                    uid:window.localStorage.getItem('uid'),
                    site_name:'',
                    sampling_num:'',
                    equipment_num:''
            },
            rules2: {
                // site_name: [
                   
                //     { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
                // ],
                // sampling_num: [
                //     { validator: checknumber1, trigger: 'blur' },
                // ],
                //   equipment_num: [
                //     { validator: checknumber2, trigger: 'blur' },
                // ],
            }
        }
    },
     methods: {
 cancel3(){
                this.$emit('closeparent100000000');
         },
      submitForm(formName) {
           this.$confirm('是否确认添加站点信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '确认添加',
                cancelButtonText: '继续添加'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '请等待...',
                });
             this.$refs[formName].validate((valid) => {
                if (valid) {
                    var url = "http://jenk.gd.cn:8080/FB/site/add";
                    var _this = this;
                    var obj = {};
                    obj.uid = _this.ruleForm2.uid;
                    obj.site_name = _this.ruleForm2.site_name;
                    obj.sampling_num = _this.ruleForm2.sampling_num;
                    obj.equipment_num = _this.ruleForm2.equipment_num;
                    ajax.inAjaxpost(url,obj,(data)=>{
                        if(data.code == 108){
                            alert('站点输入错误，请重新输入');
                            return false;
                        }
                        if(data.code == 200){
                            alert('添加成功');
                            _this.ruleForm2.site_name = '';
                            _this.ruleForm2.sampling_num = '';
                            _this.ruleForm2.equipment_num = ''; 
                            this.$emit("closeparent300000")

                        }
                    })
                } 
                })
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续添加'
                    : '停留在当前页面'
                })
            });
       
      }
     }
}
</script>
<style scoped>
.changstyle1{
    position: relative;
    box-sizing: border-box;
     border: 2px solid #000;
}

.changstyle2{
    font-size: 1.6rem;
    padding: 0.8rem;
    color:rgb(10, 10, 10);
    font-weight: bolder;
}
.el-button--primary{
    font-size: 1.2rem;
    width:80%;
    margin-bottom: 1rem;
}
.el-form{
    width:88%;
    margin:0 auto;
}

</style>




