<template>
    <div class="re">
        <!-- <span @click="showornot">显示/隐藏</span>
        <div v-if='$store.state.status'>
            6666
        </div>
        <span @click="getdata">请求</span>
        <div class="changestyle9">
        </div> -->
       <el-container >
             <el-header class="changestyle3">
                    <el-container >
                        <el-aside  width="288px"> 
                            <img src="../../static/logo.jpg" alt="用户名" width="188px" style="margin-top:2vh;">
                        </el-aside>
                        <el-main class="changestyle4">
                            <!-- <select name="" id="" @change="changelang">
                                <option value="">{{ $t('a4') }}</option>
                                <option value="zh">{{ $t('a2') }}</option>
                                <option value="en">{{ $t('a3') }}</option>
                            </select> -->
                            <el-row>
                                <div style="display:inline-block;margin-right:1rem;text-align:right;color:rgb(234, 99, 31);" @click="revealUser">{{username}}管理员，欢迎您</div>
                                <el-button round class="changestyle7" size="mini" @click="exit1">退出</el-button>
                            </el-row>
                              <el-row style="font-size:1.5rem;height:2rem;text-align:center;background-color:#409EFF;color:#fff;">
                                  欢迎您来到管理系统！
                            </el-row>
                        </el-main>
                    </el-container>
             </el-header>
            <el-aside class="changestyle1 " width="100vw"  >
                    <div style="position:absolute;z-index:11;top:6rem;left:4rem;"  >
                        <el-aside  width="20px"  v-for="(item , index) in imgs" :key="index" > 
                            <img :src="item.src" alt="图片" :width="item.width" :style="item.style" >
                        </el-aside>
                    </div>
                    <el-tabs style="height:100%;" :tab-position="tabPosition"  @tab-click="tabClick">
                        <el-tab-pane class="" :label="item.label" v-for="(item , index) in labels" :key="index"> 
                            <management  v-if="item.isRouterAlive" :tableD="tableD"  ></management>
                            <management1  v-if="item.isRouterAlive1"  :tableD1="tableD1"></management1>
                             <sampling  v-if="item.isRouterAlive2"  :tableD200="tableD200"></sampling>
                                <maintain  v-if="item.isRouterAlive3"  :tableD3="tableD3"></maintain>
                                    <station  v-if="item.isRouterAlive4"  :tableD4="tableD4"></station>
                        </el-tab-pane>
                    </el-tabs>
            </el-aside>
        </el-container>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import management from './management';
import management1 from './management1';
import sampling from './sampling';
import maintain from './maintain';
import station from './station';
import page from '../../static/page.png';
import users from '../../static/users.png';
import items from '../../static/items.png';
import polling from '../../static/polling.png';
import circle from '../../static/circle.png';
import sets from '../../static/sets.png';
// import FileSaver from 'file-saver'
// import XLSX from 'xlsx'
export default {
    name:'backstage',
    provide(){
            return{
                reload:this.reload,
            }
    },
    data(){
        return{ 
                tableD:[],
                tableD1:[],
                tableD200:[],
                tableD3:[],
                tableD4:[],
                userSelects:{
                        uid:window.localStorage.getItem('uid'),
                    },
                username:window.localStorage.getItem("saveusername"),
                levelJudge:window.sessionStorage.getItem("levelJudge"),
            //    key:window.sessionStorage.getItem("key"),
                id:'',
                tabPosition: 'left',
                labels:[
                    {
                        label:'首页',
                        numb:1,
                    },
                    {
                        label:'管理员管理',
                        isRouterAlive:true,
                        numb:2,
                    },
                       {
                        label:'取样点管理',
                        isRouterAlive2:true,
                        numb:3,
                    },
                       {
                        label:'巡检设备管理',
                          isRouterAlive1:true,
                        numb:4,
                       
                    },
                       {
                        label:'站点管理',
                        isRouterAlive4:true,
                         numb:5,
                    },
                     {
                        label:'操作记录',
                        isRouterAlive3:true,
                        numb:6,
                    },
                ],
                imgs:[
                    {
                        style:"margin-top:1.1rem;",
                        width:'20px',
                           src:  page,
                    },
                    {
                      style:"margin-top:1.8rem;",
                      
                        width:'18px',
                           src:     users,
                    },
                       {
                        style:"margin-top:2.0rem;",
                        width:'18px',
                           src:      items,
                    },
                       {
                     style:"margin-top:1.9rem;",
                        width:'18px',
                           src:    polling,
                    },
                       {
                        style:"margin-top:1.95rem;",
                         width:'18px',
                            src:   circle,
                    },
                     {
                     style:"margin-top:1.9rem;",
                        width:'18px',
                           src:   sets,
                    },
                ]
            }
    },
     methods: {
          reload(){
              
                this.labels[1].isRouterAlive = false,
                this.labels[2].isRouterAlive2 = false,
                this.labels[3].isRouterAlive1 = false,
                     this.labels[4].isRouterAlive4 = false,
                     this.labels[5].isRouterAlive3 = false,
                this.$nextTick(function(){
                    this.labels[1].isRouterAlive = true
                      this.labels[2].isRouterAlive2 = true
                        this.labels[3].isRouterAlive1 = true
                         this.labels[4].isRouterAlive4 = true
                              this.labels[5].isRouterAlive3 = true
                })
            },
         revealUser(){
              this.$router.push({
                        name: 'revealUser',
                    })
                },
                getdatas(){
                     var url = "http://jenk.gd.cn:8080/FB/user/select";
                                var obj = {};
                                var _this = this;
                                obj = _this.userSelects;
                                ajax.inAjaxpost(url,obj,(data)=>{
                                     for(var i = 0 ; i < data.user.length ; i++){
                                            if(data.user[i].level == '1') {
                                                data.user[i].level = '操作管理员';
                                            }
                                            else  if( data.user[i].level == '2') {
                                                data.user[i].level = '站点管理员';
                                            
                                            }else  if( data.user[i].level == '3') {
                                                data.user[i].level = '公司管理员';
                                            
                                            } else  if( data.user[i].level == '4') {
                                                data.user[i].level = '超级管理员';
                                            }
                                            else{
                                                return;
                                            }
                                        }
                                    for(var i = 0 ; i < data.user.length ; i++){
                                            window.sessionStorage.getItem('username');
                                            if(data.user[i].username  ==  window.localStorage.getItem('saveusername')){
                                            data.user.splice(i,1)
                                            }
                                    }
                                   _this.tableD = data.user;
                                })
                },
                   getdatas1(){
                           var url = "http://jenk.gd.cn:8080/FB/sampling/selectAll";
                                var obj = {};
                                var _this = this;
                                 obj = _this.userSelects;
                                ajax.inAjaxpost(url,obj,(data)=>{
                                    _this.tableD200 =  data.sampling;

                                })
                   },
                     getdatas2(){
                            var url = "http://jenk.gd.cn:8080/FB/equipment/select";
                                var obj = {};
                                var _this = this;
                                  obj = _this.userSelects;
                                ajax.inAjaxpost(url,obj,(data)=>{
                                   _this.tableD1 = data.equipment;
                                })
                     },
                         getdatas3(){
                                 var url = "http://jenk.gd.cn:8080/FB/record/select";
                                var obj = {};
                                var _this = this;
                                ajax.inAjax(url,obj,(data)=>{
                                   _this.tableD3 = data.equipment;
                                })
                         },
                           getdatas4(){
                            var url = "http://jenk.gd.cn:8080/FB/site/select";
                                var obj = {};
                                var _this = this;
                                 obj = _this.userSelects;
                                ajax.inAjaxpost(url,obj,(data)=>{
                                   _this.tableD4 =data.site;
                                })
                           },
         tabClick(targetName) {
                if(this.levelJudge != '4'){
                        if(this.labels[targetName.index].numb == '2'){
                            this.labels[targetName.index].isRouterAlive = false;
                            this.$alert('您不是超级管理员，所以无法操作这一项', '确认信息', {
                                confirmButtonText: '确定',
                                callback: action => {
                                    this.$message({
                                    type: 'info',
                                    message: '只有超级管理员才可以访问哟！'
                                    });
                                }
                            });
                        }
                         if(this.labels[targetName.index].numb == '4'){
                            this.labels[targetName.index].isRouterAlive1 = false;
                            this.$alert('您不是超级管理员，所以无法操作这一项', '确认信息', {
                                confirmButtonText: '确定',
                                callback: action => {
                                    this.$message({
                                    type: 'info',
                                    message: '只有超级管理员才可以访问哟！'
                                    });
                                }
                            });
                        }
                         if(this.labels[targetName.index].numb == '3'){
                             this.labels[targetName.index].isRouterAlive2 = true;
                                 this.getdatas1();
                     }
                      if(this.labels[targetName.index].numb == '5'){
                                  this.labels[targetName.index].isRouterAlive4 = true;
                                    this.getdatas4();
                       }
                        if(this.labels[targetName.index].numb == '6'){
                                  this.labels[targetName.index].isRouterAlive3 = true;
                                  this.getdatas3();
                       }
                }
                if(this.levelJudge == '4'){
                //点击  获取数据  
                     if(this.labels[targetName.index].numb == '2'){
                            this.labels[targetName.index].isRouterAlive = true;
                               this.getdatas();
                     }
                      if(this.labels[targetName.index].numb == '3'){
                             this.labels[targetName.index].isRouterAlive2 = true;
                                 this.getdatas1();
                     }
                       if(this.labels[targetName.index].numb == '4'){
                                  this.labels[targetName.index].isRouterAlive1 = true;
                                 this.getdatas2();
                       }
                       if(this.labels[targetName.index].numb == '5'){
                                  this.labels[targetName.index].isRouterAlive4 = true;
                                    this.getdatas4();
                       }
                        if(this.labels[targetName.index].numb == '6'){
                                  this.labels[targetName.index].isRouterAlive3 = true;
                                  this.getdatas3();
                       }
                }
        },
         exit1(){
             this.$emit("saveLogin");
               this.$router.push({
                 name:'login'
             });
            var url = "http://jenk.gd.cn:8080/FB/user/logout";
            var obj = {};
            ajax.inAjaxpost(url,obj,(data)=>{}
            )
         },
    },
  components:{
      management,
     management1,
     sampling,
     maintain,
     station
  
  }
}
</script>
<style >
.el-header {
    padding: 0 !important;
    box-sizing: border-box;
    -ms-flex-negative: 0;
    flex-shrink: 0;
}
.changestyle4{
    width:78vw;
    text-align: right;
    box-sizing: border-box;
}
.el-tabs--left .el-tabs__item.is-left{text-align:center !important}
.el-tabs__item.is-active {
    color: #fff !important;
    background-color: #409EFF;
}
.changestyle3{
    margin-bottom: 2rem;
}
.changestyle7{
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    border: 1px solid rgb(1,143,245);
   color:red !important;
    -webkit-appearance: none;
    text-align: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    outline: 0;
    margin: 0;
    -webkit-transition: .1s;
    transition: .1s;
    font-weight: 500;
    padding:0.2rem 1.1rem !important;
    margin-right:1.5rem !important;
    margin-bottom: 1rem !important;
    font-size: 17px !important;
    border-radius: 4px !important;
}
.el-main {
    display: block;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    -ms-flex-preferred-size: auto;
    flex-basis: auto;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
padding: 1rem 0 0.5rem 0.5rem!important;
}
.el-tabs__item {
    padding: 0 20px;
    height: 40px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    line-height: 40px;
    display: inline-block;
    list-style: none;
    font-size: 14px;
    font-weight: 500;
    color: #303133;
    background: #ccc;
    position: relative;
    margin: 0.8rem 0;
}
.el-tab-pane{
    box-sizing: border-box;
    background-color:#f5f5f5;
    padding:1rem 1rem 1rem 1.2rem;
}
/* .el-tabs--left .el-tabs__header.is-left {
    margin-right: 0px !important;
} */
.changestyle9{
    width:10px;
    height:100vh;
    background-color: #f9f9f9;
    position: absolute;
    left:288px;
    z-index: 1;
}
.el-table th>.cell{text-align:center;position:relative;word-wrap:normal;text-overflow:ellipsis;display:inline-block;vertical-align:middle;width:100%;-webkit-box-sizing:border-box;box-sizing:border-box}
.el-table .cell {
    text-align: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    white-space: normal;
    word-break: break-all;
    line-height: 23px;
}
.el-table thead{color:#fff;background-color:#ccc ;font-weight:500;}
.el-table--border,.el-table--group{border:0px solid #ebeef5}
.re{position: relative;height:100vh;}
.changestyle11{
    background-color: red;
    height:100%;
}
</style>




