<template>
    <div>
           <div style="position: absolute;top:0;left:0;bottom:0px;right:0px; background-color:#333;opacity: 0.5;z-index: 1;"></div>
        <div style="position: absolute;width: 100vw;z-index: 2;">
        <el-container>
           
            <el-main style="margin-top:2rem;">
                <el-row >
                    <el-col style="background-color:#fff;" :span="12" :offset="6">
                        <div >
                            <div class="changstyle1" >
                                  <div style="width:18px;height:18px;position:absolute;top:0rem;right:0.5rem;z-index:111">
                                          <el-button style="border:0px solid #fff;padding:0rem;" @click="cancel3">
                                                <img :src="cancel" alt="取消" width='25px'>
                                          </el-button>
                                </div>
                                <el-row>
                                    <el-col :span="24"><div class="changstyle2">添加</div></el-col>
                                </el-row>
                               <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="6rem" >
                                   
                                    <el-form-item style="line-height:-1rem;" label="用户名" prop="username">
                                        <el-input placeholder="请输入用户名" v-model="ruleForm2.username"></el-input>
                                    </el-form-item>
                                    <el-form-item style="line-height: 1rem!important;" label="密码" prop="password">
                                        <el-input type="password" placeholder="请输入用户密码" v-model="ruleForm2.password" autocomplete="off"></el-input>
                                    </el-form-item>
                                    <el-form-item label="确认密码" prop="checkPass">
                                        <el-input type="password" placeholder="请再次确认密码" v-model="ruleForm2.checkPass" autocomplete="off"></el-input>
                                    </el-form-item>
                                    <el-form-item label="电话" prop="telphone">
                                        <el-input  type="tel" placeholder="请输入用户电话" v-model.number="ruleForm2.telphone"></el-input>
                                    </el-form-item>
                                   <el-form-item label="备注" prop="remark">
                                        <el-input placeholder="请输入用户备注" v-model="ruleForm2.remark"></el-input>
                                    </el-form-item>
                                    <el-form-item label="等级" prop="level">
                                        <el-input placeholder="请输入用户等级（1操作管理员,2站点管理员,3公司管理员,4超级管理员）" v-model="ruleForm2.level"></el-input>
                                    </el-form-item>
                                     <el-form-item label="账号归属地" prop="affiliation" >
                                        <el-input placeholder="请输入用户归属地:  X X 市" v-model="ruleForm2.affiliation"></el-input>
                                    </el-form-item>
                                     <el-form-item label="是否登录" prop="login" >
                                        <el-radio v-model="ruleForm2.login" label="是">是</el-radio>
                                        <el-radio v-model="ruleForm2.login" label="否">否</el-radio>
                                    </el-form-item>
                                        <el-button type="primary" @click="submitForm('ruleForm2')">添加</el-button>
                                </el-form>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
        </div>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import cancel from '../../static/cancel.png';
export default {
  
    name:'register',
    data(){
         var checknumber = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('电话不能为空'));
        }
        setTimeout(() => {
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            }else if(value < 10000000000){
                    callback(new Error('请输入11位数字的电话号码'));
            }else  if(value > 100000000000){
                callback(new Error('请输入11位数字的电话号码'));
            }else{
                callback();
            }
            }, 1000);
        };
        var checkLevel = (rule, value, callback) => {
            if (!value) {
            return callback(new Error('等级不能为空'));
            }else if(value == '操作管理员' || value == '站点管理员' || value == '公司管理员' || value == '超级管理员' ) {
                callback();
            }else if (value != '操作管理员' || value != '站点管理员' || value != '公司管理员' || value != '超级管理员' ) {
                callback(new Error('请输入正确的内容'));
            }
        };
         var validatePass = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('请输入密码'));
            } else {
            if (this.ruleForm2.checkPass !== '') {
                this.$refs.ruleForm2.validateField('checkPass');
            }
            callback();
            }
        };

        var validateaffiliation = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('请输入用户归属地'));
            } else {
            callback();
            }
        };
         var validateremark = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('请输入用户备注'));
            } else {
            callback();
            }
        };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm2.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
         return{
             cancel,
            ruleForm2: {
                       uid:window.localStorage.getItem('uid'),
                id:'',
                password: '',
                checkPass: '',
                telphone: '',
                remark: '',
                username: '',
                level:'',
                affiliation:'',
                login:'是',
            },
            rules2: {
                password: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                checkPass: [
                    { validator: validatePass2, trigger: 'blur' }
                ],
                username: [
                    { required: true,message: '请输入用户名', trigger: 'blur' },
                      { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
                    
                ],
                remark: [
                    { validator: validateremark, trigger: 'blur' },
                    { mix:1, max:50,message: '请输入用户备注1-50个字符', trigger: 'blur' }
                ],
                telphone: [
                    { validator: checknumber, trigger: 'blur' },
                  
                ],
                level: [
                    { validator: checkLevel, trigger: 'blur' },
                ],
                affiliation: [
                    { validator: validateaffiliation, trigger: 'blur' },
                    { mix:1, max:50,message: '请输入用户归属地', trigger: 'blur' }
                ],
            }
        }
    },
     methods: {
         cancel3(){
                this.$emit('closeparent3');
         },
      submitForm(formName) {
           this.$confirm('是否确认添加管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '确认添加',
                cancelButtonText: '继续添加'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '请等待...',
                });
             this.$refs[formName].validate((valid) => {
                if (valid) {
                    var url = "http://jenk.gd.cn:8080/FB/user/add";
                    var _this = this;
                    let Base64 = require('js-base64').Base64;
                    _this.ruleForm2.password = Base64.encode(_this.ruleForm2.password);
                    _this.ruleForm2.checkPass = Base64.encode(_this.ruleForm2.checkPass);
                    var obj = {};
                    obj.password = _this.ruleForm2.password;
                    obj.telphone = _this.ruleForm2.telphone;
                      obj.uid = _this.ruleForm2.uid;
                    obj.remark = _this.ruleForm2.remark;
                    obj.username = _this.ruleForm2.username;
                    obj.affiliation = _this.ruleForm2.affiliation; 
                    obj.login = _this.ruleForm2.login;  
                    if( _this.ruleForm2.level == '操作管理员') {
                        obj.level = 1 
                    }
                    else if(  _this.ruleForm2.level == '站点管理员') {
                        obj.level = 2 
                    }
                    else if(  _this.ruleForm2.level == '公司管理员') {
                        obj.level = 3 
                    }
                    else if(  _this.ruleForm2.level == '超级管理员') {
                        obj.level= 4 
                    }
                    ajax.inAjaxpost(url,obj,(data)=>{
                        if(data.code == 108){
                            alert('用户名已经存在请重新输入');
                            return false;
                        }
                        if(data.code == 200){
                            alert('添加成功');
                        _this.ruleForm2.password = '';
                        _this.ruleForm2.telphone = '';
                        _this.ruleForm2.remark = '';
                            _this.ruleForm2.username = ''; 
                            _this.ruleForm2.level= '';
                        _this.ruleForm2.checkPass= '';
                            _this.ruleForm2.affiliation= '';
                            _this.ruleForm2.login= '是';
                            this.$emit('closeparent1');
                        }
                    })
                } 
                })
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续添加'
                    : '停留在当前页面'
                })
            });
       
      }
     }
}
</script>
<style scoped>
.changstyle1{
    position: relative;
    box-sizing: border-box;
     border: 2px solid #000;
}

.changstyle2{
    font-size: 1.6rem;
    padding: 0.8rem;
    color:rgb(10, 10, 10);
    font-weight: bolder;
}
.el-button--primary{
    font-size: 1.2rem;
    width:80%;
    margin-bottom: 1rem;
}
.el-form{
    width:88%;
    margin:0 auto;
}

</style>




