<template>
    <div>
        <div style="position: absolute;top:0;left:0;bottom:0px;right:0px; background-color:#333;opacity: 0.5;z-index: 1;"></div>
        <div style="position: absolute;width: 100vw;z-index: 2;">
        <el-container>
                           
            <el-main >
                <el-row >
                    <el-col style="background-color:#fff;margin-top: 4rem;" :span="12" :offset="6">
                        <div >
                            <div class="changstyle1">
                                 <div style="width:18px;height:18px;position:absolute;top:0rem;right:0.5rem;z-index:111">
                                          <el-button style="border:0px solid #fff;padding:0rem;" @click="cancel2">
                                                <img :src="cancel" alt="取消" width='25px'>
                                          </el-button>
                                </div>
                                <el-row>
                                    <el-col :span="24"><div class="changstyle2">修改</div></el-col>
                                </el-row>
                               <el-form :model="loginUsername" status-icon :rules="rules2" ref="loginUsername" label-width="6rem" >
                                    <el-form-item  label="站点名" prop="site_name">
                                        <el-input placeholder="请输入站点名" v-model="loginUsername.site_name"></el-input>
                                    </el-form-item>
                                    <el-form-item label="取样数量" prop="sampling_num">
                                        <el-input  placeholder="请输入取样数量" v-model="loginUsername.sampling_num" ></el-input>
                                    </el-form-item>
                                    <el-form-item label="设备数量" prop="equipment_num">
                                        <el-input  type="tel" placeholder="请输入设备数量" v-model.number=" loginUsername.equipment_num"></el-input>
                                    </el-form-item>
                                        <el-button type="primary" @click="submitForm('loginUsername')">修改</el-button>
                                </el-form>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
        </div>
    </div>
</template>
<script>
import ajax from '../common/ajax';
import cancel from '../../static/cancel.png';
export default {
    name:'modificationstation',
    data(){
         return{
              loginUsername: {
                  id:window.sessionStorage.getItem("s_id"),
                    uid:window.localStorage.getItem('uid'),
                    site_name:'',
                    sampling_num:'',
                    equipment_num:''
            },
                  cancel,
            rules2: {
                // password: [
                //     { validator: validatePass, trigger: 'blur' }
                // ],
                // username: [
                //     { message: '请输入用户名', trigger: 'blur' },
                // ],
                // remark: [
                //     { validator: validateremark, trigger: 'blur' },
                //     { mix:1, max:50,message: '请输入用户备注1-50个字符', trigger: 'blur' }
                // ],
                // telphone: [
                //     { validator: checknumber, trigger: 'blur' },
                // ],
                // level: [
                //     { validator: checkLevel, trigger: 'blur' },
                // ],
             
                // affiliation: [
                //     { validator: validateaffiliation, trigger: 'blur' },
                //     { mix:1, max:50,message: '请输入用户归属地', trigger: 'blur' }
                // ],
            }
        }
    },
     methods: {
         cancel2(){
             this.$emit("closeparent200000")
         },
      submitForm(formName) {
           this.$confirm('是否确认修改管理员信息', '确认信息', {
                distinguishCancelAndClose: true,
                confirmButtonText: '确认修改',
                cancelButtonText: '继续修改'
            })
            .then(() => {
                this.$message({
                type: 'info',
                message: '已经修改',
                });
                   this.$refs[formName].validate((valid) => {
                    if (valid) {
                    var url = "http://jenk.gd.cn:8080/FB/site/update";
                    var obj = {};
                    obj.uid = this.loginUsername.uid;
                    obj.id = this.loginUsername.id;
                    obj.site_name = this.loginUsername.site_name;
                    obj.sampling_num = this.loginUsername.sampling_num;
                    obj.equipment_num = this.loginUsername.equipment_num;
                    ajax.inAjaxpost(url,obj,(data)=>{
                        if(data.code == 108){
                            alert('修改失败');
                            return false;
                        }
                        if(data.code == 200){
                             this.$emit("closeparent100000")
                        }
                    })
                } 
                })
            })
            .catch(action => {
                this.$message({
                type: 'info',
                message: action === 'cancel'
                    ? '继续修改'
                    : '停留在当前页面'
                })
            });
        
      }
     },

}
</script>
<style scoped>
.changstyle1{
    position: relative;
    box-sizing: border-box;
     border: 2px solid #000;
}
.changstyle2{
    font-size: 1.6rem;
    padding: 1.5rem;
    color:rgb(10, 10, 10);
    font-weight: bolder;
}
.el-button--primary{
    font-size: 1.2rem;
    width:80%;
    margin: 1rem 0;
}
.el-form{
    width:88%;
    margin:0 auto;
}
</style>




